.card-button {
  position: relative;
  font-size: 16px;
  line-height: 20px;
  min-height: 40px;
  text-transform: uppercase;
  text-align: center;

  &__link {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #68b738;
    width: calc(100% - 106px);
    padding: 8px 53px;
    color: #ffffff;
    text-decoration: none;
  }

  &__new {
    background-color: #f2f2f2;
    color: #444444;
  }
}
